import React from 'react';
import withBem from 'hoc/withBem';

import './style.scss';

interface Card {
    className?: string;
    children: React.ReactNode;
    index?: number | string | false;
    badge: string;
    bem: BemRenderer;
    size?: number;
}

const Card: React.FC<Card> = ({ children, className = '', index, badge, bem, size }) => {
    const style = size ? { width: size, height: size } : {};
    return (
        <div className={bem.all(bem(), className)} style={style}>
            {index && <span className={bem('index')}>{index}</span>}
            {typeof badge !== 'undefined' && <span className={bem('badge')}>{badge}</span>}
            {children}
        </div>
    );
};

export default withBem(Card, 'Card');
