import { applySnapshot } from 'mobx-state-tree';

import { StoreType } from './types';
import { routes } from './ui';

interface VpsStockItem {
    name: string;
    clear_name: string; // eslint-disable-line camelcase
    guid: string;
    price: string;
    images_urls: string[]; // eslint-disable-line camelcase
}

interface AddItemsCommand {
    stock: VpsStockItem[];
}

const showItemsCast = (store: StoreType, command: AddItemsCommand): void => {
    const stock = Object.values(command.stock);
    const items = stock.map((item: VpsStockItem) => ({
        id: item.guid,
        name: item.name,
        clearName: item.clear_name,
        price: Number(item.price),
        guid: item.guid,
        imagesUrls: item.images_urls,
    }));

    applySnapshot(store.products, {
        title: 'Попкорн',
        items,
    });
    store.ui.isLoading = false;
};

interface VpsAddItem extends AssistantSdkCommand {
    item: string;
    count: number;
}

const addItemCast = (store: StoreType, command: VpsAddItem): void => {
    const { item: itemName, count } = command;
    const item = store.products.items.find(({ name }) => name === itemName);

    let products = count;
    if (item) {
        while (products) {
            store.cart.addToCart(item.id);
            products -= 1;
        }
        store.products.selectItem(item.id);
    }
};

interface RemoveItemFromCartCommand extends AssistantSdkCommand {
    item: string;
    count: number;
}

const removeItem = (store: StoreType, { item, count }: RemoveItemFromCartCommand) =>
    store.cart.removeFromCart({ itemId: item, count, shouldRemoveMention: true });

const removeAll = (store: StoreType) => store.cart.clear();

const gotoCart = (store: StoreType): void => {
    store.ui.gotoPage(routes.CART);
};

interface GotoCatalogCommand extends AssistantSdkCommand {
    clear: boolean;
}

const gotoCatalog = (store: StoreType, command: GotoCatalogCommand): void => {
    if (command.clear) {
        store.cart.clear();
    }

    store.ui.gotoPage(routes.CATALOG);
};

interface ValidationOkCommand extends AssistantSdkCommand {
    order_id: string; // eslint-disable-line camelcase
}

const success = (store: StoreType, command: ValidationOkCommand): void => {
    store.cart.orderId = command.order_id;
    store.ui.gotoPage(routes.STATUS);
};

const casts: { [key: string]: Function } = {
    SHOW_ITEMS: showItemsCast,
    ADD_ITEM: addItemCast,
    REMOVE_ITEM: removeItem,
    REMOVE_ALL: removeAll,
    GO_TO_VALIDATION: gotoCart,
    GO_TO_CATALOG: gotoCatalog,
    VALIDATION_OK: success,
};

export default casts;
