import { types, Instance, SnapshotIn, cast, SnapshotOut } from 'mobx-state-tree';

import ProductItem from './product';

const CartItem = types.model('CartItem', {
    product: types.reference(ProductItem),
    count: types.number,
});

export type CartItemModel = Instance<typeof CartItem>;

const Cart = types
    .model('Cart', {
        items: types.array(CartItem),
        orderId: types.maybeNull(types.string),
    })
    .views((self) => ({
        get count(): number {
            return self.items.reduce((acc: number, item: CartItemModel) => acc + item.count, 0);
        },

        get totalPrice(): number {
            return self.items.reduce((acc: number, item: CartItemModel) => acc + item.product.price * item.count, 0);
        },

        get itemsMap(): { [key: string]: CartItemModel } {
            return self.items.reduce((acc: Record<string, CartItemModel>, item: CartItemModel) => {
                acc[item.product.id] = item;

                return acc;
            }, {});
        },
    }))
    .actions((self) => {
        const cart = self;

        const addToCart = (itemId: string): void => {
            const item = cart.items.find((item) => item.product.id === itemId);

            if (item) {
                item.count++;
            } else {
                cart.items.push({
                    product: itemId,
                    count: 1,
                });
            }
        };

        const clear = (): void => {
            cart.items = cast([]);
        };

        interface RemoveFromCartParams {
            itemId: string;
            count: number;
            shouldRemoveMention?: boolean;
        }

        const removeFromCart = ({ itemId, count, shouldRemoveMention }: RemoveFromCartParams) => {
            const index = cart.items.findIndex((item: CartItemModel) => item.product.id === itemId);
            const item = cart.items[index];

            if (!item) {
                return;
            }

            item.count = Math.max(0, item.count - count);

            if (!item.count && shouldRemoveMention) {
                cart.items.splice(index, 1);
            }
        };

        const confirmPayment = (): void => {
            window.AssistantClient.sendMessage({
                type: 'server_action',
                /* eslint-disable @typescript-eslint/camelcase */
                server_action: {
                    action_id: 'make_purchase',
                },
                /* eslint-enable @typescript-eslint/camelcase */
            });
        };

        return {
            clear,
            addToCart,
            removeFromCart,
            confirmPayment,
        };
    });

export type CartModel = Instance<typeof Cart>;
export type CartSnapshotOut = SnapshotOut<typeof Cart>;
export type CartSnapshotIn = SnapshotIn<typeof Cart>;

export default Cart;
