import React from 'react';
import { applySnapshot } from 'mobx-state-tree';
import { USER_MOCK } from 'mocks';
import { isProduction } from 'configs';
import Container from 'components/Grids/Container';
import Row from 'components/Grids/Row';
import Col from 'components/Grids/Col';
import { Suggest } from 'components/Suggest/Suggest';
import { Header } from 'components/Header/Header';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';

import { CartInfo } from './Info/CartInfo';
import { CartList } from './List/CartList';
import './Cart.scss';

type CartProps = StoreType;

const Component: React.FC<CartProps> = ({ user }) => {
    React.useEffect(() => {
        const onKeyDown = (event: any) => {
            switch (event.code) {
                case 'Digit1':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ITEM',
                            item: '32e5b884-fa98-11e8-80c5-0cc47a817925',
                            count: 2,
                        },
                    });
                    break;
                case 'Digit2':
                    applySnapshot(user, USER_MOCK);
                    break;
                default:
                    break;
            }
        };

        document.addEventListener('keydown', onKeyDown);

        return () => {
            document.removeEventListener('keydown', onKeyDown);
        };
    }, [user]);

    return (
        <>
            <Header title="Корзина">
                <HeaderAddress />
            </Header>
            <Container className="CartPage__container">
                <Row className="CartPage__row">
                    <Col s={2} m={3} l={4} xl={6}>
                        <CartList />
                    </Col>
                    <Col s={2} m={3} l={4} xl={6} className="CartPage__text">
                        <CartInfo />
                    </Col>
                </Row>
            </Container>
            {!isProduction ? <Suggest items={['1. Удали два средних черничных попкорна', '2. Дополнить адрес']} /> : ''}
        </>
    );
};

export const Cart = connectToStore(Component);
