import { types, applySnapshot } from 'mobx-state-tree';
import { environment, isProduction } from 'configs';
import { USER_MOCK } from 'mocks';

import user from './user';
import ui, { routes } from './ui';
import products from './products';
import cart from './cart';
import { StoreType } from './types';
import casts from './casts';

const Store = types
    .model('Store', {
        ui: types.optional(ui, {}),
        products: types.optional(products, {}),
        cart: types.optional(cart, {}),
        user: types.optional(user, {}),
    })
    .actions((self) => {
        const store = self;

        const afterCreate = function init() {
            applySnapshot(store.user, {
                address: USER_MOCK.address,
            });
            store.ui.isLoading = true;

            if (window.AssistantHost && typeof window.AssistantHost.ready === 'function') {
                window.AssistantHost.ready();
            }

            if (environment !== 'production') {
                // Здесь можно восмользоваться моком для локальной разработки, напр.
                // applySnapshot(store.products, PRODUCTS_MOCK)
                // store.ui.isLoading = false;
            }
        };

        const onMessage = (message: AssistantSdkCommand): void => {
            const { command } = message.smart_app_data;

            if (command in casts) {
                const castFn = casts[command];

                castFn(store, message.smart_app_data);
            } else {
                // eslint-disable-next-line no-underscore-dangle
                window.__messages.push({
                    'not-fount': message,
                });
            }
        };

        return { onMessage, afterCreate };
    });

const store = Store.create({});

function castFromStore(store: StoreType): AppStateInterface {
    return {
        items:
            store.ui.page === routes.CATALOG
                ? store.products.items.map((product) => ({
                      number: product.index,
                      title: product.name,
                      visibles: product.visible,
                      id: product.id,
                      count_in_cart: store.cart.itemsMap[product.id] ? store.cart.itemsMap[product.id].count : 0, // eslint-disable-line
                      action: {},
                  }))
                : [],
        current_screen: store.ui.page === routes.CATALOG ? 'catalog' : 'cart', // eslint-disable-line
        foodtech: {
            products: Object.keys(store.cart.itemsMap).map((itemId) => ({
                id: itemId,
                quantity: store.cart.itemsMap[itemId].count,
            })),
            totalPrice: {
                value: store.cart.totalPrice,
                discountValue: 0,
            },
        },
    };
}

if (!isProduction) {
    window.AssistantHost = window.AssistantHost || {};
    window.AssistantHost.sendMessage = (some) => some;
    window.AssistantHost.updateState = (some) => some;
}

window.AssistantClient = window.AssistantClient || {};

/**
 * Assistand SDK API implementation:
 */
window.AssistantClient.onStart = function AssistantSdkOnStart(): void {
    // eslint-disable-next-line no-underscore-dangle
    window.__messages = window.__messages || [];
};

window.AssistantClient.onData = function AssistantSdkOnMessage(command: AssistantSdkCommand): void {
    store.onMessage(command);
};

// TODO Описать схему сообщений в AssistantSDK
window.AssistantClient.sendMessage = function AssistantSdkSendMessage(message: any): void {
    window.AssistantHost.sendMessage(JSON.stringify(message));
};

window.AssistantClient.onRequestState = function AssistantSdkOnRequestState(): void {
    const state = castFromStore(store);
    window.AssistantHost.updateState(JSON.stringify(state));
};

export default store;
