import React from 'react';
import Card from 'components/Card';
import withBem from 'hoc/withBem';
import { ProductItemModel } from 'stores/product';

import './CatalogListItem.scss';

interface CatalogListItemProps extends ProductItemModel, BemProps {
    countInCart: number;
    onClick: () => void;
}

const Component: React.FC<CatalogListItemProps> = ({ bem, imagesUrls, countInCart, name, price, index, onClick }) => (
    <div tabIndex={0} className={bem()} onClick={onClick}>
        <Card index={index} className={bem('card')}>
            <img className={bem('img')} src={imagesUrls[0]} alt="" />
        </Card>
        <div className={bem('description')}>
            <span className={bem('callToAction')} />
            <div className={bem('row')}>
                <span className={bem('title', 'name')}>{name}</span>
            </div>
            <div className={bem('priceRow')}>
                <span className={bem('title', 'price')}>{price} ₽</span>
                {countInCart ? <span className={bem('title', 'badge')}>{countInCart}</span> : ''}
            </div>
        </div>
    </div>
);

export const CatalogListItem = withBem(Component, 'CatalogListItem');
