import React from 'react';
import { connectToStore } from 'tools/connect';
import withBem from 'hoc/withBem';
import { StoreType } from 'stores/types';
import { ProductItemModel } from 'stores/product';

import { CatalogListItem } from '../ListItem/CatalogListItem';
import './CatalogList.scss';

interface CatalogListProps extends StoreType, BemProps {
    childKeyPrefix: string;
}

const Component: React.FC<CatalogListProps> = ({ bem, products, cart }) => (
    <div className={bem()}>
        <div className={bem('container')}>
            {products.items.map((item: ProductItemModel, index) => (
                <CatalogListItem
                    {...item}
                    countInCart={cart.itemsMap[item.id] ? cart.itemsMap[item.id].count : 0}
                    key={item.id}
                    index={index + 1}
                    onClick={() => cart.addToCart(item.id)}
                />
            ))}
        </div>
    </div>
);

export const CatalogList = withBem(connectToStore(Component), 'CatalogList');
