import React from 'react';
import { StoreType } from 'stores/types';
import withBem from 'hoc/withBem';
import { connectToStore } from 'tools/connect';

import './Header.scss';

interface HeaderProps extends BemProps, StoreType {
    title: string;
    subtitle?: string;
}

const Component: React.FC<HeaderProps> = ({ bem, ui, title, subtitle, children }) => (
    <div className={bem()}>
        {ui.previous ? <span className={bem('back')} /> : ''}
        <div className={bem('section')}>
            <div className={bem('logo')} />
            <div className={bem('title-container')}>
                <div className={bem('title')}>{title}</div>
                {subtitle ? <div className={bem('subtitle')}>{subtitle}</div> : ''}
            </div>
        </div>
        <div className={bem('section')}>{children}</div>
    </div>
);

export const Header = connectToStore(withBem(Component, 'Header'));
