import React from 'react';
import { environment } from 'configs';
import { PRODUCTS_MOCK } from 'mocks';
import { Suggest } from 'components/Suggest/Suggest';
import Container from 'components/Grids/Container';
import Row from 'components/Grids/Row';
import Skeleton from 'components/Skeleton';
import ContentLoader from 'components/ContentLoader';
import { Header } from 'components/Header/Header';
import { HeaderAddress } from 'components/HeaderAddress/HeaderAddress';
import { HeaderCart } from 'components/HeaderCart/HeaderCart';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';

import { CatalogList } from './List/CatalogList';

type CatalogProps = StoreType;

const Component: React.FC<CatalogProps> = ({ ui, cart }) => {
    React.useEffect(() => {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line
            smart_app_data: {
                command: 'SHOW_ITEMS',
                stock: PRODUCTS_MOCK,
            },
        });

        console.log('sme');

        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line
            smart_app_data: {
                command: 'ADD_ITEM',
                item: 'Средний черничный попкорн',
                count: 2,
            },
        });
    }, []);

    React.useEffect(() => {
        const onKeyDown = (event: any) => {
            switch (event.code) {
                case 'Digit1':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'SHOW_ITEMS',
                            stock: PRODUCTS_MOCK,
                        },
                    });
                    break;
                case 'Digit2':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ITEM',
                            item: '32e5b884-fa98-11e8-80c5-0cc47a817925',
                            count: 2,
                        },
                    });
                    break;
                case 'Digit3':
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'REMOVE_ALL',
                        },
                    });
                    break;
                default:
            }
        };

        document.addEventListener('keydown', onKeyDown);

        return () => {
            document.removeEventListener('keydown', onKeyDown);
        };
    }, []);

    const hasItemsInCart = cart.count > 0;

    const onLoading = (
        <Container>
            <Row>
                <Skeleton backgroundColor="#262828" foregroundColor="#414545" />
            </Row>
        </Container>
    );

    const suggest =
        environment !== 'production' ? (
            <Suggest items={['1. Показать каталог', '2. Удали два средних черничных попкорна', '3. Удали все']} />
        ) : (
            ''
        );

    return (
        <>
            <Header title="Самокат" subtitle="«Хочу еду к просмотру»">
                <HeaderAddress />
                {hasItemsInCart && <HeaderCart />}
            </Header>
            <CatalogList />
            {suggest}
        </>
    );
    // <ContentLoader isActive={ui.isLoading} onLoading={onLoading}>
    // </ContentLoader>
};

export const Catalog = connectToStore(Component);
