import React from 'react';
import { CartItemModel } from 'stores/cart';
import Card from 'components/Card';
import Typography from 'components/Typography';
import withBem from 'hoc/withBem';

import './CartListItem.scss';

interface CartListItemProps extends CartItemModel, BemProps {
    index: number;
    onMoreClick: () => void;
    onLessClick: () => void;
}

const Component: React.FC<CartListItemProps> = ({
    index,
    onLessClick,
    onMoreClick,
    product: { imagesUrls, clearName, price, name },
    count,
    bem,
}) => (
    <tr className={bem()}>
        <td className={bem('cell', 'index')}>{index}</td>
        <td className={bem('cell', 'imgCell')}>
            <Card>
                <img src={imagesUrls[0]} alt={clearName} className={bem('img')} />
            </Card>
        </td>
        <td className={bem('description', 'cell')}>
            <div className={bem('row')}>
                <Typography type="title">{name}</Typography>
            </div>
            <div>
                <Typography type="subheaderDescription">{price}₽</Typography>
            </div>
        </td>
        <td className={bem('quantity', 'cell')}>
            <div className={bem('quantityIcons')}>
                <div className={bem('quantitySlider')}>
                    <div tabIndex={0} onClick={onLessClick} className={bem.modify({}, 'qtyItems')}>
                        -
                    </div>
                    <div className={bem.modify({}, 'qtyItems')}>
                        <span className={bem('qtyItem')}>{count}</span>
                    </div>
                    <div tabIndex={0} onClick={onMoreClick} className={bem.modify({}, 'qtyItems')}>
                        +
                    </div>
                </div>
            </div>
        </td>
    </tr>
);

export const CartListItem = withBem(Component, 'CartListItem');
