import React from 'react';
import withBem from 'hoc/withBem';

import './style.scss';

type TypographyType = 'header' | 'subheader' | 'body';
type DecorationType = 'strikeThrough' | 'underline';
interface TypographyProps {
    children: React.ReactNode;
    className?: string;
    type: TypographyType;
    bem: BemRenderer;
    decoration?: DecorationType;
}

const Typography: React.FC<TypographyProps> = ({ children, className = '', type, bem, decoration }) => {
    const decorationClass = decoration ? bem.mod(decoration) : '';

    return <span className={bem.all(bem(), bem.mod(type), className, decorationClass)}>{children}</span>;
};

export default withBem(Typography, 'Typography');
