import React from 'react';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';
import { CART_MOCK, PRODUCTS_MOCK } from 'mocks';
import withBem from 'hoc/withBem';

import { CartListItem } from '../ListItem/CartListItem';

import './CartList.scss';

interface CartListProps extends StoreType, BemProps {}

const Component: React.FC<CartListProps> = ({ cart, bem }) => {
    React.useEffect(() => {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line
            smart_app_data: {
                command: 'SHOW_ITEMS',
                stock: PRODUCTS_MOCK,
            },
        });

        CART_MOCK.forEach((item) => {
            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'ADD_ITEM',
                    item,
                    count: 1,
                },
            });
        });
    }, []);

    return (
        <div className={bem()}>
            <div className={bem('tableWrapper')}>
                <table className={bem('table')}>
                    <tbody>
                        {cart.items.map((item, index) => (
                            <CartListItem
                                {...item}
                                onLessClick={() => cart.removeFromCart({ itemId: item.product.id, count: 1 })}
                                onMoreClick={() => cart.addToCart(item.product.id)}
                                index={index + 1}
                                key={item.product.id}
                            />
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export const CartList = withBem(connectToStore(Component), 'CartList');
