import React from 'react';

import './style.scss';

interface RowProps {
    children: React.ReactNode;
    className?: string;
    verticalPadding?: string | number;
    topPadding?: string | number;
    bottomPadding?: string | number;
    noGutters?: boolean;
    style?: React.CSSProperties;
    [key: string]: any;
}

const Row: React.FC<RowProps> = ({
    children,
    className = '',
    noGutters = false,
    verticalPadding,
    topPadding,
    bottomPadding,
    style = {},
    ...props
}) => {
    const styles = {
        ...style,
        paddingTop: `${verticalPadding || topPadding}px`,
        paddingBottom: `${verticalPadding || bottomPadding}px`,
    };

    const elClassname = `sb-Row ${noGutters ? 'sb-Row_noGutters' : ''}`;

    return (
        <div style={styles} className={`${elClassname} ${className}`} {...props}>
            {children}
        </div>
    );
};

export default Row;
